import menu from './menu.json';
import menuTemplate from '../templat/menu-item.hbs'
import './styles.css';

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const refs = {
  jsMenu: document.querySelector('.js-menu'),
  body: document.body,
  selector: document.querySelector('.js-switch-input')
};


function buildMenu(menu) {
  const collect = menu.map(post => menuTemplate(post)).join('');
  refs.jsMenu.insertAdjacentHTML('beforeend', collect);
}

buildMenu(menu);


const currentTheme = localStorage.getItem('theme');

if(currentTheme) {
  refs.body.classList.add(currentTheme);
};
if(currentTheme === Theme.DARK) {
  refs.selector.checked = true;
};

function handleChangeInput () {
  switch (refs.selector.checked) {
    case !refs.body.classList.contains(Theme.LIGHT || Theme.DARK):
      refs.body.classList.add(Theme.LIGHT);
    case (refs.selector.checked = true):
      refs.body.classList.replace(Theme.LIGHT, Theme.DARK);
      localStorage.setItem('theme', Theme.DARK);
      break;
    case (refs.selector.checked = false):
      refs.body.classList.replace(Theme.DARK, Theme.LIGHT);
      localStorage.setItem('theme', Theme.LIGHT);
      break;
  }
};
refs.selector.addEventListener('change', handleChangeInput);
